<template>
    <div class="card" style="height: 95%;">
        <div class="card__title">
            <b-row>
            <b-col>
                <span>OCA Settlement</span>
            </b-col>
            <b-col style="max-width: max-content !important;" class="col-right">
                <span>
                  <ABSButton
                    :text="'Print'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doPrint"
                  />
                </span>
                <span>
                    <ABSButton
                        :text="'View All'"
                        classButton="button button--back"
                        classIcon="icon-style-1"
                        @click="onViewAll"
                    />
                </span>
            </b-col>
            </b-row>
        </div>
        <div class="card__body">
          <div>
              <ACCFormList
                  :prop="propList"
                  :title="''"
                  @rowClicked="rowClicked"
                  ref="ref_OCASettlementList"
              >
                <template slot="paid" slot-scope="data">
                  <div style="text-align:right;">
                      <span>{{isCurrency(data.item.paid, decimal)}}</span>
                  </div>
                  </template>
                  <template slot="unpaid" slot-scope="data">
                  <div style="text-align:right;">
                      <span>{{isCurrency(data.item.unpaid, decimal)}}</span>
                  </div>
                  </template>
                  <template slot="status" slot-scope="data">
                  <b-row>
                      <b-col>
                      <div 
                          :class="'Plan-Dot-' + (Status[data.item.status].variant)"
                          :style="(data.item.status == 'POD' ? 'font-size: 9px !important;' : 'font-size: 10px !important;') +  'padding-top: 9px !important;'">
                          {{data.item.status}}</div>
                      </b-col>
                  </b-row>
                  </template>
              </ACCFormList>
          </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/OP/OP_CostSettlement",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' AND unpaid > 0",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
      Status: {
        NW: { key: "NW", label: "New", variant: "Magenta" },
        AS: { key: "AS", label: "Assign", variant: "Lime" },
        DP: { key: "DP", label: "Dispatch", variant: "Green" },
        AR: { key: "AR", label: "Arrived", variant: "Purple" },
        SL: { key: "SL", label: "Start Loading", variant: "Blue" },
        FL: { key: "FL", label: "Finish Loading", variant: "Orange" },
        GO: { key: "GO", label: "Get Out", variant: "Red" },
        AD: { key: "AD", label: "Arrive Destination", variant: "Gold" },
        SU: { key: "SU", label: "Start Unloading", variant: "LightGreen" },
        FU: { key: "FU", label: "Finish Unloading", variant: "Pink" },
        // GD: { key: "GD", label: "Get Out Destination", variant: "Red" },
        POD: { key: "POD", label: "POD", variant: "LightGreen2" },
      },
    };
  },
  computed: {},
  methods: {
    rowClicked(record, index) {
        var param = record;
        this.$store.commit("setParamPage", param);
        this.$router.push({ name: "OP_CostSettlementView" });
    },
    RenderData() {
      this.$refs.ref_OCASettlementList.doGetList("");
    },
    doPrint() {

    },
    onViewAll() {
        this.$router.push({ name: "OP_CostSettlement" });
    },
  },
  mounted() {
    this.RenderData();
  },
};
</script>

<style scoped>
</style>